.conteiner {
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    display: inline;
    margin: 10px 0;
    font-size: 2rem;
    color: #2196f3;
    opacity: 1;
    transition: opacity .25s linear;
}

.title:hover,
.title:focus {
    opacity: 0.8;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.appear {
  transform: translateX(-100%);
}

.appearActive {
  transform: translateX(0);
  transition: all 500ms linear;
}